.App {
  text-align: center; }

.App-logo {
  width: 100%;
  max-width: 100%;
  height: auto;
  position: fixed;
  top: -100px;
  left: 0;
  z-index: 1000; }

.App__background-wrapper {
  min-height: 66.67vh;
  overflow: hidden; }

.App-header {
  min-height: 66.67vh; }

.App-intro {
  font-size: large; }

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

.NavBar div {
  background: #ffffff;
  position: relative;
  z-index: 10000;
  height: 70px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  color: red; }



